.privacy {
	margin-top: 66px;
}
.privacy .title {
	font-weight: 700;
	font-size: 48px;
	line-height: 47.25px;
	text-align: center;
	text-transform: uppercase;
	margin-bottom: 34px;
}
.privacy .description {
	font-weight: 400;
	font-size: 20px;
	line-height: 28px;
	color: #716f6f;
}
@media screen and (max-width: 500px) {
	.privacy {
		margin-top: 32px;
	}
	.privacy .title {
		font-size: 24px;
		margin-bottom: 18px;
	}
	.privacy .description {
		font-size: 18px;
		line-height: 20px;
	}
}
