.download {
	padding: 66px 0;
	background-color: #fbfbfb;
}
.download .title {
	font-weight: 700;
	font-size: 48px;
	line-height: 47.25px;
	text-transform: uppercase;
	margin-bottom: 24px;
}
.download .description {
	font-weight: 400;
	font-size: 16px;
	line-height: 24px;
	color: #6c6c72;
	width: 55%;
	margin: auto;
	margin-bottom: 26px;
	width: 100%;
}
.download .rating {
	display: flex;
	gap: 30px;
}
.download .rating .rating-card {
	box-shadow: 0px 0px 12px 0px #00000026;
	padding: 22px 27px;
	text-align: center;
	width: 170px;
	border-radius: 10px;
}
@media screen and (max-width: 500px) {
	.download {
		padding: 32px 0;
	}
	.download .title {
		font-size: 24px;
		margin-bottom: 18px;
	}
	.download .rating {
		gap: 12px;
		margin-bottom: 2rem;
	}
	.download .rating .rating-card {
		padding: 12px 8px;
		width: 100%;
	}
}
