nav {
	width: 90%;
	position: absolute;
	top: 35px;
	left: 50%;
	transform: translate(-50%, 0);
	z-index: 9;
}
nav .topnavbar {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
nav .topnavbar .email-phone {
	display: flex;
	gap: 47px;
	align-items: center;
}
nav .topnavbar .email-phone div {
	display: flex;
	gap: 18px;
	align-items: center;
}
nav .topnavbar .social {
	display: flex;
	align-items: center;
	gap: 24px;
}
.navbar {
	position: relative;
	box-shadow: 0px 0px 12px 0px #00000026;
	background: #fff;
	padding: 7px 32px;
	margin-top: 26px;
	border-radius: 5px;
}
.navbar .nav-logo {
	position: absolute;
	left: 50%;
	transform: translate(-50%, 0);
	box-shadow: 0px 0px 12px 0px #00000026;
	padding: 18px 40px;
	background-color: #fff;
	border-radius: 5px;
}
.navbar .navbar-nav {
	display: flex;
	justify-content: space-between;
}

.navbar .navbar-expand-lg {
	width: 100%;
}
.navbar .navbar-expand-lg .navbar-nav {
	width: 100%;
	display: flex;
	justify-content: space-between;
}
.navbar .navbar-expand-lg .navbar-nav div {
	display: flex;
	justify-content: space-between;
	width: calc(46% - 100px);
	align-items: center;
}
.navbar .navbar-expand-lg .navbar-nav .nav-link {
	font-weight: 600;
	font-size: 20px;
	line-height: 30px;
	text-transform: uppercase;
	text-decoration: none;
	color: #000;
}
.navbar .navbar-expand-lg .navbar-nav .nav-link.active {
	color: var(--primary-color);
}
.navbar .navbar-expand-lg .navbar-nav .download {
	background: var(--primary-color);
	color: #fff;
	border-radius: 5px;
	height: 50px;
	font-weight: 700;
	line-height: 2;
	padding: 10px 15px;
	text-transform: uppercase;
	text-decoration: none;
}
nav .topnavbar .email-phone .email a,
nav .topnavbar .email-phone .phone a {
	color: #000;
	text-decoration: none;
}
@media screen and (max-width: 900px) {
	.navbar {
		display: flex;
		flex-direction: row-reverse;
		justify-content: space-between;
		align-items: start;
		padding: 7px 16px;
		margin-top: 36px;
	}
	.navbar .navbar-collapse {
		margin-top: 5rem;
	}
	.navbar .navbar-collapse .navbar-nav {
		width: 100%;
		display: flex;
	}
	.navbar .navbar-collapse .navbar-nav div {
		display: flex;
		flex-direction: column;
		width: 100%;
		align-items: start;
	}
	.navbar .nav-logo {
		position: relative;
		box-shadow: none;
		padding: 0;
		left: auto;
		transform: none;
	}
	.navbar .nav-logo img {
		height: 50px;
	}
	.navbar .navbar-expand-lg {
		width: auto;
	}
	.navbar .navbar-expand-lg button.navbar-toggler {
		position: absolute;
		top: 12px;
		left: 16px;
	}
	.navbar .navbar-expand-lg .navbar-nav .download {
		color: #000;
		background: none;
		padding: 10px 0;
	}
	/* .collapse:not(.show) {
		display: block;
	} */
}
@media screen and (max-width: 500px) {
	nav {
		top: 20px;
	}
	nav .topnavbar .email-phone {
		flex-direction: column;
		gap: 8px;
		align-items: start;
	}
	nav .topnavbar .email-phone span {
		font-size: 16px;
	}
	nav .topnavbar .email-phone div {
		gap: 8px;
	}
	nav .topnavbar .email-phone .phone {
		display: none;
	}
	nav .topnavbar .social {
		gap: 8px;
	}
}
