.about {
	padding: 88px 0;
	overflow: hidden;
}
.about .title {
	text-align: center;
	font-weight: 700;
	font-size: 48px;
	line-height: 47.25px;
	text-transform: uppercase;
	margin-bottom: 24px;
}
.about .description {
	font-weight: 400;
	font-size: 16px;
	line-height: 24px;
	text-align: center;
	color: #6c6c72;
	width: 55%;
	margin: auto;
	margin-bottom: 42px;
}
.about .about-data {
	display: flex;
	gap: 150px;
	justify-content: center;
}
.about .about-data .iphone-image {
	position: relative;
}
.about .about-data .iphone-image .iphone {
	width: 240px;
	height: 570px;
	transform: rotate(-15deg);
	/* position: absolute; */
}
.about .about-data .iphone-image .aboutImage {
	width: 208px;
	height: 555px;
	transform: rotate(-15deg);
	position: absolute;
	z-index: 2;
	left: 0;
	border-radius: 20px;
}

.about .about-data .about-description {
	display: flex;
	flex-direction: column;
	gap: 40px;
}
.about .about-data .about-description-card {
	box-shadow: 0px 0px 12px 0px #00000026;
	padding: 20px;
	border-radius: 10px;
	display: flex;
	gap: 18px;
	align-items: start;
}
.about .about-data .about-description-card .title {
	font-weight: 700;
	font-size: 20px;
	line-height: 30px;
	text-transform: uppercase;
	color: #232233;
	margin-bottom: 12px;
	text-align: start;
}

.about .about-data .about-description-card .description {
	font-weight: 400;
	font-size: 16px;
	line-height: 24px;
	width: 100%;
	margin: 0;
	text-align: start;
}
@media screen and (max-width: 900px) {
	.about {
		padding: 60px 0;
	}
	.about .description {
		width: 90%;
	}
	.about .about-data svg {
		transform: rotate(15deg) translateX(-34px);
	}
}
@media screen and (max-width: 500px) {
	.about {
		padding: 40px 0;
	}
	.about .title {
		font-size: 24px;
		margin-bottom: 12px;
	}
	.about .description {
		width: 100%;
		margin-bottom: 0;
	}
	.about .about-data svg {
		width: 100%;
	}
	.about .about-data .about-description-card {
		padding: 12px;
		gap: 10px;
	}
	.about .about-data .about-description-card .description {
		line-height: 22px;
	}
	.about .about-data .about-description {
		gap: 24px;
	}
}
