header {
	/* min-height: 1000px; */
	position: relative;
	background-color: #fbfbfb;
}
header .Vector {
	position: absolute;
	width: 80%;
	height: 90%;
	top: 5%;
	left: 10%;
}
.header {
	padding-top: 260px;
	padding-bottom: 131px;
}
.header .squire {
	border: 18px solid var(--primary-color);
	width: 95%;
	position: relative;
	/* z-index: 9; */
	height: fit-content;
}
.header .squire .title-head {
	margin: 50px;
	margin-left: -50px;
	background-color: #fff;
	padding: 40px 30px;
}
.header .squire .title-head .title {
	font-weight: 700;
	font-size: 48px;
	line-height: 130%;
	text-transform: uppercase;
	color: var(--primary-color);
	margin-bottom: 18px;
}
.header .squire .title-head .description {
	color: #6c6c72;
	font-size: 16px;
	font-weight: 400;
	line-height: 24px;
	margin-bottom: 50px;
}
.header .squire .title-head .download-now {
	color: #232233;
	font-weight: 600;
	font-size: 25px;
	line-height: 37.5px;
	margin-bottom: 16px;
	text-transform: uppercase;
}
.header .squire .title-head .download-apps {
	display: flex;
	gap: 14px;
}
.phone-head .phone {
	position: relative;
	width: 300px;
	height: 605px;
}
.phone-head .phone img {
	position: absolute;
}
.phone-head .phone .iphone {
	width: 100%;
	height: 100%;
	z-index: 2;
	top: 0;
	left: 0;
}
.phone-head .phone .screen {
	width: 89%;
	height: 94%;
	z-index: 1;
	left: 17px;
	top: 20px;
}
@media screen and (max-width: 900px) {
	.header {
		padding-top: 230px;
		padding-bottom: 80px;
	}
	.header .squire {
		width: 100%;
		border-width: 12px;
	}
	.header .squire .title-head {
		margin: 0;
		padding: 30px 20px;
	}
	.header .squire .title-head .title {
		font-size: 32px;
		line-height: 100%;
	}
	.header .squire .title-head .description {
		margin-bottom: 30px;
	}
	.header .squire .title-head .download-now {
		font-size: 20px;
	}
	.header .squire .title-head .download-apps {
		/* flex-direction: column; */
		gap: 8px;
	}
	.header .squire .title-head .download-apps img {
		width: 50%;
	}
}
@media screen and (max-width: 500px) {
	.header {
		padding-top: 180px;
		padding-bottom: 50px;
	}
	.header .squire {
		width: 100%;
		border-width: 8px;
		margin-bottom: 1.5rem;
	}
	.header .squire .title-head {
		margin: 0;
		padding: 20px 15px;
	}
	.header .squire .title-head .title {
		font-size: 32px;
		line-height: 100%;
	}
	.header .squire .title-head .description {
		line-height: 22px;
		margin-bottom: 30px;
	}
	.header .squire .title-head .download-now {
		font-size: 22px;
		margin-bottom: 12px;
	}
	.header .justify-center-mobile {
		justify-content: center !important;
	}
}
