.features {
	background-color: #fbfbfb;
	padding: 88px 0;
}
.features .title {
	text-align: center;
	font-weight: 700;
	font-size: 48px;
	line-height: 47.25px;
	text-transform: uppercase;
	margin-bottom: 24px;
}
.features .description {
	font-weight: 400;
	font-size: 16px;
	line-height: 24px;
	text-align: center;
	color: #6c6c72;
	width: 55%;
	margin: auto;
	margin-bottom: 42px;
}
.features .features-data {
}
.features .feature-card {
	width: 75%;
}
.features .feature-card .title {
	font-weight: 600;
	font-size: 24px;
	line-height: 30px;
	vertical-align: bottom;
	text-transform: uppercase;
	color: #000;
	margin-bottom: 10px;
	margin-top: 16px;
}
.features .feature-card .description {
	width: 100%;
	margin-bottom: 0;
}
.features .iphone-card {
	position: relative;
	text-align: center;
}
.features .iphone-card .iphone {
	filter: drop-shadow(0px 0px 12px #000000cc);
}
.features .iphone-card .bg {
	position: absolute;
	top: 20px;
	left: 45px;
	width: 79%;
	height: 95%;
	border-radius: 40px;
}
@media screen and (max-width: 900px) {
	.features .description {
		width: 100%;
		line-height: 24px !important;
	}
	.features .features-data .order1 {
		order: 1;
	}
	.features .features-data .order5 {
		order: 2;
	}
	.features .features-data .order2 {
		order: 3;
	}
	.features .features-data .order3 {
		order: 5;
	}
	.features .features-data .order4 {
		order: 4;
	}
	.features .col-md-6 .d-flex {
		padding: 0px !important;
		margin: 0px !important;
	}
	.features .feature-card {
		margin-bottom: 1rem !important;
		margin-top: 0 !important;
		text-align: center !important;
		width: 100% !important;
	}
	.features .feature-card .title,
	.features .feature-card .description {
		text-align: center !important;
	}
	.features .order3 {
		margin-top: 3rem;
		width: 100% !important;
	}
	.features .iphone-card .bg {
		width: 46%;
		left: 27%;
	}
}
@media screen and (max-width: 500px) {
	.features {
		padding: 40px 0;
	}
	.features .title {
		font-size: 24px;
		margin-bottom: 16px;
	}
	.features .description {
		width: 100%;
		line-height: 22px !important;
	}
	.features .feature-card {
		display: flex;
		gap: 20px;
		width: 100%;
		text-align: start !important;
	}
	.features .feature-card .title,
	.features .feature-card .description {
		text-align: start !important;
	}
	.features .feature-card {
		margin-bottom: 1rem !important;
		margin-top: 0 !important;
	}
	.features .feature-card img {
		width: 36px;
	}
	.features .feature-card .title {
		font-size: 18px;
		margin-bottom: 0;
	}
	.features .features-data .order1 {
		order: 1;
	}
	.features .features-data .order2 {
		order: 2;
	}
	.features .features-data .order3 {
		order: 5;
	}
	.features .features-data .order4 {
		order: 3;
	}
	.features .features-data .order5 {
		order: 4;
	}
	.features .iphone-card {
		width: 75%;
		margin: 3rem auto 0;
	}
	.features .iphone-card .iphone {
		width: 100% !important;
	}
	.features .iphone-card .bg {
		position: absolute;
		top: 14px;
		left: 17px;
		width: 87%;
		height: 95%;
		border-radius: 26px;
	}
}
