.screenshots {
	padding: 88px 40px;
	overflow: hidden;
}
.screenshots .title {
	text-align: center;
	font-weight: 700;
	font-size: 48px;
	line-height: 47.25px;
	text-transform: uppercase;
	margin-bottom: 24px;
}
.screenshots .description {
	font-weight: 400;
	font-size: 16px;
	line-height: 24px;
	text-align: center;
	color: #6c6c72;
	width: 55%;
	margin: auto;
	margin-bottom: 42px;
}

.screenshots .custom-swiper {
	position: relative;
	width: 100%;
	margin: auto;
	text-align: center;
	display: flex;
	justify-content: center;
	gap: 24px;
}

.screenshots .slides {
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
	width: 90%;
}

.screenshots .slide {
	/* transition: all 0.8s ease-in-out; */
	margin: 0 10px;
	width: 100%;
	position: relative;
}
.screenshots .slide.active {
	z-index: 2;
}
.screenshots .phone-wrapper {
	display: flex;
	justify-content: center;
	align-items: center;
}
.screenshots .phone-wrapper img {
	width: 250px;
	height: 500px;
	border-radius: 20px;
	filter: drop-shadow(0px 0px 5px #00000040);
}
.screenshots .slide.active .phone-wrapper img {
	height: 550px;
}
.screenshots .iphone-frame {
	background: url("../../assets/iphone.png") no-repeat center;
	background-size: contain;
	padding: 50px 20px;
}
.screenshots .arrow {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	background: #c46b2f;
	color: #fff;
	border: none;
	padding: 10px 15px;
	cursor: pointer;
	border-radius: 50%;
	font-size: 20px;
	width: 64px;
	height: 64px;
	font-size: 1.5rem;
}
.screenshots .left {
	left: 0px;
}
.screenshots .right {
	right: 0px;
}
.left-slide:first-of-type {
	transform: translateX(100px);
}
.right-slide:last-of-type {
	transform: translateX(-100px);
}
.slide:nth-of-type(4) {
	z-index: 99;
}
.screenshots .dots {
	text-align: center;
}
.screenshots .dot {
	height: 12px;
	width: 12px;
	margin: 0 4px;
	background-color: var(--primary-color);
	border-radius: 50%;
	display: inline-block;
	cursor: pointer;
}
.screenshots .dot.active {
	height: 15px;
	width: 15px;
	background-color: #c46b2f;
}
@media screen and (max-width: 900px) {
	.screenshots {
		padding: 30px 10px;
	}
	.screenshots .description {
		width: 100%;
		padding: 0 20px;
	}
	.screenshots .arrow {
		width: 32px;
		height: 32px;
		top: auto;
		bottom: -40px;
		padding: 0;
	}
	.screenshots .slides {
		width: 100%;
	}
	.screenshots .right {
		right: 16px;
	}
	.screenshots .left {
		left: 16px;
	}
	.download .rating {
		gap: 18px;
	}
	.download .rating .rating-card {
		padding: 20px 18px;
	}
}
@media screen and (max-width: 500px) {
	.screenshots {
		padding: 40px 0;
	}
	.screenshots .title {
		font-size: 24px;
		margin-bottom: 16px;
		line-height: 100%;
		padding: 0 20px;
	}
	.screenshots .description {
		width: 100%;
		margin-bottom: 20px;
		padding: 0 20px;
	}
}
