footer {
	background-color: #fbfbfb;
	box-shadow: 0px 0px 12px 0px #00000026;
	padding: 57px 0 14px;
	margin-top: 65px;
}
footer .social {
	display: flex;
	gap: 30px;
}
footer .social svg {
	fill: var(--primary-color);
}
footer .social .separte {
	height: 20px;
	width: 1px;
	background-color: var(--primary-color);
}
footer .description {
	font-weight: 400;
	font-size: 16px;
	line-height: 24px;
	color: #9b9a9a;
	margin-top: 20px;
	margin-bottom: 22px;
	padding-right: 3rem;
}
footer .links a {
	margin-top: 12px;
	font-weight: 400;
	font-size: 16px;
	line-height: 24px;
	color: #000;
	text-decoration: none;
	display: block;
}
footer .title {
	font-weight: 600;
	font-size: 24px;
	line-height: 37.5px;
	text-transform: uppercase;
	color: #000;
	margin-bottom: 11px;
}
footer .subscribe-input {
	background-color: #fff;
	border-radius: 10px;
	padding: 5px 8px;
	display: flex;
	align-items: center;
	gap: 8px;
}
footer .subscribe-input input {
	border: 0;
	outline: 0;
	width: 100%;
}
footer .power {
	font-weight: 400;
	font-size: 14px;
	line-height: 100%;
	color: #000;
	text-align: center;
	margin: 0;
}
footer .power a {
	color: #000;
	text-decoration: none;
}
@media screen and (max-width: 900px) {
	footer .social {
		gap: 12px;
	}
	footer .description {
		padding: 0;
	}
}
@media screen and (max-width: 500px) {
	footer {
		padding: 24px 0 16px;
		margin-top: 62px;
	}
	footer .col-md-4:first-of-type {
		text-align: center;
		margin-bottom: 1rem;
	}
	footer .col-md-4:first-of-type img {
		margin-bottom: 1rem;
	}
	footer .col-md-4:first-of-type .description {
		padding: 0;
	}
	footer .social {
		justify-content: center;
		gap: 12px;
	}
	footer .links {
		margin-bottom: 1.5rem;
	}
	footer .title {
		font-size: 18px;
		margin-bottom: 8px;
	}
	footer .links div {
		display: grid;
		grid-template-columns: auto auto;
		gap: 8px;
	}
	footer .links a {
		margin: 0;
	}
	footer .description {
		margin-top: 0;
	}
}
